:root {
  --PrimaryColor: hsl(13, 83%, 45%);
  --SecondaryColor: hsl(13, 80%, 60%);
  --gradientColor: linear-gradient(to right, hsl(13, 80%, 60%), hsl(13, 83%, 45%));
  --whiteColor: hsl(42, 11%, 83%);
  --blackColor: hsl(195, 25%, 6%);
  --textColor: hsl(240, 6%, 7%);
  --whiteColorDeam: hsl(40, 24%, 78%);
  --greyText: rgb(75, 72, 71);
  --inputColor: rgb(167, 155, 152);
  --bodyColor: rgb(104, 101, 98);
  --cardBG: rgb(226, 204, 184);
}

.main .secTitle .title {
  position: relative;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 1rem 0;
  color: var(--whiteColorDeam);
  z-index: 2;
  border-bottom: 1px solid var(--whiteColorDeam);
}
.main .secContent {
  justify-content: center;
  gap: 1.5rem;
}
.main .secContent .singleDestination {
  height: 100%;
  display: grid;
  row-gap: 10px;
  border-radius: 10px;
  background: var(--whiteColorDeam);
  box-shadow: 0 2px 4px rgba(101, 101, 104, 0.549);
  overflow: hidden;
  align-items: center;
  cursor: pointer;
}
.main .secContent .singleDestination:hover {
  background: var(--cardBG);
  box-shadow: 1 4px 4px rgba(85, 85, 114, 0.549);
  transition: 0.3s ease;
}
.main .secContent .singleDestination .imageDiv {
  width: 100%;
  overflow: hidden;
}
.main .secContent .singleDestination .imageDiv img {
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  transition: 2s ease;
}
.main .secContent .singleDestination .imageDiv:hover img {
  transform: scale(1.2);
}
.main .secContent .singleDestination .cardInfo {
  padding: 1rem;
}
.main .secContent .singleDestination .cardInfo .destTitle {
  color: var(--blackColor);
  font-size: 1.25rem;
}
.main .secContent .singleDestination .cardInfo .continent {
  gap: 0.5rem;
}
.main .secContent .singleDestination .cardInfo .continent .icon {
  color: var(--textColor);
  font-size: 18px;
}
.main .secContent .singleDestination .cardInfo .continent .name {
  color: var(--textColor);
  font-weight: 400;
  font-size: 14px;
}
.main .secContent .singleDestination .fees {
  width: 100%;
  justify-content: space-between;
  padding: 1rem;
  margin: 1rem 0;
  border-top: 1.5px solid var(--greyText);
  border-bottom: 1.5px solid var(--greyText);
}
.main .secContent .singleDestination .fees .grade {
  color: var(--textColor);
  max-width: 100px;
  line-height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main .secContent .singleDestination .fees .grade small {
  border-radius: 1rem;
  background: var(--gradientColor);
  font-size: 10px;
  padding: 2px 10px;
  color: var(--textColor);
}
.main .secContent .singleDestination .fees .price {
  font-size: 2rem;
  color: var(--blackColor);
}
.main .secContent .singleDestination .desc {
  font: 13.5px;
  color: var(--textColor);
  font-weight: 600;
  justify-content: space-between;
  gap: 0.5rem;
}
.main .secContent .singleDestination .btn {
  color: var(--textColor);
  font-weight: 600;
  margin-top: 1rem;
  justify-content: space-between;
  gap: 0.5rem;
}
.main .secContent .singleDestination .btn .icon {
  align-self: center;
  font-size: 18px;
}

@media screen and (min-width: 769px) {
  .title {
    font-size: 1.5rem;
  }
  .title::after {
    width: 155px !important;
  }
  .secContent {
    grid-template-columns: repeat(2, 1fr);
  }
  .secContent .singleDestination {
    height: auto;
  }
}
@media screen and (min-width: 900px) {
  .secContent {
    grid-template-columns: repeat(3, 1fr);
  }
  .secContent .singleDestination {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
  }
}
